import React, { useState } from "react";
import postMessageToParent from "./postMessageToParent";
import "./styles.css";

export default function CVV() {
  const [value, setValue] = useState("");

  const handleChange = (change) => {
    const { value } = change.target;

    if (/^[0-9]{0,4}$/.test(value)) {
      setValue(value);
      postMessageToParent({
        from: "cvv",
        messageType: "validation",
        data: {
          valid: /^[0-9]{4}$/.test(value),
        },
      });
    }
  };

  const handleFocus = () => {
    postMessageToParent({
      from: "cvv",
      messageType: "event",
      data: {
        eventName: "onfocus",
        value: true,
      },
    });
  };

  const handleBlur = () => {
    postMessageToParent({
      from: "cvv",
      messageType: "event",
      data: {
        eventName: "onblur",
        value: true,
      },
    });
  };

  return (
    <input
      type="text"
      placeholder="CVV"
      name="cvv"
      value={value}
      onChange={handleChange}
      onFocus={handleFocus}
      onBlur={handleBlur}
    />
  );
}
